export interface IMortgage {
    rate: number;
    points: number;
    term: number;
    loanAmount: number;
    loanBaseAmount: number;

    /**
     * principal interest tax insurance
     */
    piti: number;
    pAndI: number;
}

const getMoney = (value: any) => {
    value = value || 0;
    return (Math.round(parseFloat(value) * 100)) / 100;
}

export class Mortgage implements IMortgage {
    rate: number;
    points: number;
    term: number;
    loanAmount: number;
    piti: number;
    pAndI: number;
    loanBaseAmount: number;
    constructor() {
        this.rate = 0;
        this.points = 0;
        this.term = 0;
        this.loanAmount = 0;
        this.loanBaseAmount = 0;
        this.piti = 0;
        this.pAndI = 0;
    }

}
export interface ICredits {
    credit1Name: string;
    credit1Amount: number;
    credit2Name: string;
    credit2Amount: number;
    credit3Name: string;
    credit3Amount: number;
    credit4Name: string;
    credit4Amount: number;
    totalCredits: number;
}
export class Credits implements ICredits {
    credit1Name: string;
    credit1Amount: number;
    credit2Name: string;
    credit2Amount: number;
    credit3Name: string;
    credit3Amount: number;
    credit4Name: string;
    credit4Amount: number;
    totalCredits: number;
    constructor() {
        this.credit1Name = '';
        this.credit1Amount = null;
        this.credit2Name = '';
        this.credit2Amount = null;
        this.credit3Name = '';
        this.credit3Amount = null;
        this.credit4Name = '';
        this.credit4Amount = null;
    }

}
export interface IPrepaids {
    monthsOfTax: number;
    monthsOfTaxAmount: number;
    monthsOfInsurance: number;
    monthsOfInsuranceAmount: number;
    daysOfInterest: number;
    daysOfInterestAmount: number;
    totalPrePaids: number;
}
export class PrePaids implements IPrepaids {
    monthsOfTax: number;
    monthsOfTaxAmount: number;
    monthsOfInsurance: number;
    monthsOfInsuranceAmount: number;
    daysOfInterest: number;
    daysOfInterestAmount: number;
    totalPrePaids: number;
    constructor() {
        this.monthsOfTax = 0;
        this.monthsOfInsurance = 0;
        this.daysOfInterest = 0;

    }

}
export interface ILoanOption {
    expiry?: String;
    optionNo: number;
    salesPrice: number;
    loanMode: boolean;
    loanType: number;
    downPayment: number;
    miType: number;

    tax: number;
    ins: number;
    hoa: number;
    mi: number;

    taxVerified: boolean;
    insVerified: boolean;
    hoaVerified: boolean;
    miVerified: boolean;


    other: number;

    firstMortgage: IMortgage;
    front: number;
    back: number;
    dti: number;
    ltv: number;
    cltv: number;
    secondMortgage: IMortgage;
    firstAndSecondPiti: number;
    prePaids: IPrepaids;
    credits: ICredits;
    closingCosts: IClosingCost[];
    totalClosingCosts: number;
    cashToClose: number;
    apr: number;
}
export class LoanOption implements ILoanOption {
    expiry?: String;
    taxVerified: boolean;
    insVerified: boolean;
    hoaVerified: boolean;
    miVerified: boolean;
    tax: number;
    ins: number;
    hoa: number;
    mi: number;
    other: number;
    credits: ICredits;
    prePaids: IPrepaids;
    optionNo: number;
    salesPrice: number;
    loanMode: boolean;
    loanType: number;
    downPayment: number;
    miType: number;
    firstMortgage: IMortgage;
    front: number;
    back: number;
    dti: number;
    ltv: number;
    cltv: number;
    secondMortgage: IMortgage;
    firstAndSecondPiti: number;
    closingCosts: IClosingCost[] = [];
    cashToClose: number;

    totalClosingCosts: number;
    apr: number;
    constructor() {
        this.salesPrice = 0;
        this.hoa = 0;
        this.other = 0;
        this.dti = 0;
        this.firstMortgage = new Mortgage();
        this.secondMortgage = new Mortgage();
        this.prePaids = new PrePaids();
        this.credits = new Credits();
        this.loanMode = true;
        this.loanType = 1;
        this.miType = 1;
        this.ltv = 0;
        this.cltv = 0;
        const c1 = new ClosingCost();
        c1.name = 'Discount Points';
        c1.mode = 1;
        c1.rowNo = 1;
        this.closingCosts.push(c1);
        const c2 = new ClosingCost();
        c2.name = 'Title Insurance';
        c2.mode = 2;
        c2.rowNo = 2;
        this.closingCosts.push(c2);
        //FOR THE FHA and VA 
        const c3 = new ClosingCost();
        c3.name = 'UFMIP (FHA)';
        c3.mode = 1;
        c3.rowNo = 3;
        c3.percentage = 1.75;
        this.closingCosts.push(c3);

        const c4 = new ClosingCost();
        c4.name = 'VA - (Purchase)';
        c4.mode = 1;
        c4.rowNo = 4;
        c4.percentage = 2.15;
        this.closingCosts.push(c4);
        const c5 = new ClosingCost();
        c5.name = 'VA - (Re-Finance)';
        c5.mode = 1;
        c5.rowNo = 5;
        c5.percentage = 0.500;
        this.closingCosts.push(c5);
        //END FHA and VA 
        for (let index = 6; index <= 15; index++) {
            const c = new ClosingCost();
            c.rowNo = index;
            this.closingCosts.push(c);
        }
        this.totalClosingCosts = 0;
        this.cashToClose = 0;
        this.apr = 0;

        this.tax = getMoney(6715 / 12);
        this.ins = getMoney(850 / 12);
        this.taxVerified = false;
        this.insVerified = false;
        this.hoaVerified = false;
        this.miVerified = false;
        this.expiry = null;
        // this.expiry = new Date().toLocaleDateString();
        // this.term = 0;      
    }

}
export interface IClosingCost {
    /**
     * no from 1-15
     */
    rowNo: number;
    /**
     * 1:Buyer
     * 2:Seller
     * 3:N/A
     */
    type: number;
    name: string;
    percentage?: number;
    value?: number;
    /**
    * 1:[both value and percentage]
    * 2:[value only]
    */
    mode: number;
    include: boolean;
    isApr: boolean;
    isVisible: boolean;
}
export class ClosingCost implements IClosingCost {
    isVisible: boolean;
    isApr: boolean;
    rowNo: number;
    include: boolean;
    /**
     * 1:[both value and percentage]
     * 2:[value only]
     */
    mode: number;
    type: number;
    name: string;
    percentage?: number;
    value?: number;
    /**
     *
     */
    constructor(param?: IClosingCost) {
        this.isVisible = true;
        if (param) {
            this.name = param.name || '';
            this.type = param.type || 1;
            this.value = param.value || null;
            this.percentage = param.percentage || null;
            this.mode = param.mode || 2;
            this.rowNo = param.rowNo;
            this.isApr = param.isApr;
        }
        else {
            this.name = '';
            this.type = 1;
            // this.value = 0;
            this.percentage = null;
            this.mode = 2;
            this.value = null;
            this.isApr = false;
        }
    }
}



