import { EventEmitter, Inject, Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import { Borrower, Loan, PrePaids, IPrepaids, ClosingCost, IClosingCost, Credits, ICredits, ITitleInsurance, ITitleInsuranceRange, IBpmi, IBpmiAdjustment } from './models/index';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../../../environments/environment';
import { ApiService } from '../../../shared/services/index';


@Injectable()
export class SettingsService {
  request$: EventEmitter<any>;

  private headers;
  private borroweresUrl;
  private translations: Array<string>;


  private handleError(error: any): Promise<any> {
    this.request$.emit('finished');
    return Promise.reject(error.message || error);
  }

  constructor(
    private http: Http,
    private apiService: ApiService,
  ) {
    this.request$ = new EventEmitter();
    this.headers = new Headers({ 'Content-Type': 'application/json' });
  }
  private setHeaders(): Headers {
    const headersConfig = {
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    };

    // if (this.jwtService.getToken()) {
    //   headersConfig['Authorization'] = `Token ${this.jwtService.getToken()}`;
    // }
    return new Headers(headersConfig);
  }
  private formatErrors(error: any) {
    return Observable.throw(error.json());
  }
  getPrepaids(): Observable<PrePaids> {
    this.request$.emit('starting');
    return this.apiService.get('/settings/prepaid/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data === undefined ? new PrePaids() : response.data as PrePaids;
      })
      .catch(this.handleError);
  }
  savePrepaids(prepaids: IPrepaids): Observable<PrePaids> {
    this.request$.emit('starting');
    return this.apiService.post('/settings/prepaid/save', prepaids)
      .map(response => {
        this.request$.emit('finished');
        return response as PrePaids;
      })
      .catch(this.handleError);
  }
  getClosingCosts(): Observable<ClosingCost> {
    this.request$.emit('starting');
    return this.apiService.get('/settings/closing-costs/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data === undefined ? [] : response.data as ClosingCost[];
      })
      .catch(this.handleError);
  }
  saveClosingCosts(closingCosts: IClosingCost[]): Observable<ClosingCost[]> {
    this.request$.emit('starting');
    return this.apiService.post('/settings/closing-costs/save', closingCosts)
      .map(response => {
        this.request$.emit('finished');
        return response;
      })
      .catch(this.handleError);
  }

  getCredits(): Observable<Credits> {
    this.request$.emit('starting');
    return this.apiService.get('/settings/credit/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data === undefined ? new Credits() : response.data as Credits;
      })
      .catch(this.handleError);
  }
  saveCredits(credits: ICredits): Observable<Credits> {
    this.request$.emit('starting');
    return this.apiService.post('/settings/credit/save', credits)
      .map(response => {
        this.request$.emit('finished');
        return response.data as Credits;
      })
      .catch(this.handleError);
  }


  /**lookups */

  geAllTitleInsurancePremium(): Observable<ITitleInsurance[]> {
    this.request$.emit('starting');
    return this.apiService.get('/lookup/title-insurance-premium/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data as ITitleInsurance[];
      })
      .catch(this.handleError);
  }
  geAllTitleInsurancePremiumRange(): Observable<ITitleInsuranceRange[]> {
    this.request$.emit('starting');
    return this.apiService.get('/lookup/title-insurance-range/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data as ITitleInsuranceRange[];
      })
      .catch(this.handleError);
  }
  getBpmi(): Observable<IBpmi[]> {
    this.request$.emit('starting');
    return this.apiService.get('/lookup/bpmi/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data as IBpmi[];
      })
      .catch(this.handleError);
  }

  getBpmiAdjustments(): Observable<IBpmiAdjustment[]> {
    this.request$.emit('starting');
    return this.apiService.get('/lookup/bpmi/get')
      .map(response => {
        this.request$.emit('finished');
        return response.data as IBpmiAdjustment[];
      })
      .catch(this.handleError);
  }
}



