import { Component, OnInit, Input } from '@angular/core';
import { IIncome, Income, IBorrower } from './../shared/models/index';
import { IncomeService } from 'app/routes/loan/shared/borrower.service';

@Component({
    selector: 'app-income-details',
    templateUrl: './income-details.component.html',
    styleUrls: ['./income-details.component.scss']
})
export class IncomeDetailsComponent {

    @Input()
    incomes: IIncome[];
    @Input()
    borrowers: IBorrower[];
    // frequency: string[] = ['Weekly', 'Every 2 Weeks'];
    constructor(
        private incomeService: IncomeService
    ) {
        this.incomes = new Array<Income>();
    }
    public addIncome() {
        const income = new Income();
        income.isActive = true;
        this.incomes.push(income);
        this.incomeChange(income);
    }
    public incomeChange(_income: any) {
        this.incomeService.incomeChange(_income);
    }
}



