import { Component, OnInit, Input } from '@angular/core';
import { ILoanOption, LoanOption, IIncome, IBorrower, IDebt } from 'app/routes/loan/shared/models';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import {
    AmortizationScheduleContainerComponent
} from "app/routes/loan/amortization-schedule-container/amortization-schedule-container.component";

@Component({
    selector: 'app-loan-option-container',
    templateUrl: './loan-option-container.component.html',
    styleUrls: ['./loan-option-container.component.scss']
})
export class LoanOptionContainerComponent {
    @Input() viewMode: boolean;
    @Input()
    incomes: IIncome[];
    @Input()
    debts: IDebt[];
    @Input()
    borrowers: IBorrower[];
    @Input() loanOptions: ILoanOption[];

    bsModalRef: BsModalRef;

    public showAmortizationSchedule() {
        this.bsModalRef = this.modalService.show(AmortizationScheduleContainerComponent, { 'class': 'modal-lg' });
        // const leftOption = this.loanOptions.filter(x => x.optionNo === this.leftContext);
        this.bsModalRef.content.LoanOptionLeft = this.loanOptions.filter(x => x.optionNo === this.leftContext)[0];
        this.bsModalRef.content.LoanOptionRight = this.loanOptions.filter(x => x.optionNo === this.rightContext)[0];

    }

    // CAROUSEL PROPS
    public leftContext = 1;
    public rightContext = 2;

    public nextLoanOption(side: string) {
        if (side === 'left') {
            if (this.leftContext <= this.loanOptions.length) {
                this.leftContext++;
            } else {
                this.leftContext = 1;
            }
        } else {
            if (this.rightContext <= this.loanOptions.length) {
                this.rightContext++;
            } else {
                this.rightContext = 1;
            }
        }
    }
    public prevLoanOption(side: string) {
        if (side === 'left') {
            if (this.leftContext > 1) {
                this.leftContext--;
            }
            else {
                this.leftContext = this.loanOptions.length + 1;
            }
        } else {
            if (this.rightContext > 1) {
                this.rightContext--;
            }
            else {
                this.rightContext = this.loanOptions.length + 1;
            }
        }
    }
    public addLoanOption(side: string) {
        const loanOption = new LoanOption();
        const loanOptionNo = this.loanOptions.length + 1;
        loanOption.optionNo = loanOptionNo;
        this.loanOptions.push(loanOption);
        if (side === 'left') {
            this.leftContext = loanOptionNo;
        } else {

        }
    }
    constructor(private modalService: BsModalService) {
    }
}



