import { Component, OnInit, Input } from '@angular/core';
import { IIncome, IDebt, Income, IBorrower, ILoan, Debt } from './../shared/models/index';
import { BorrowerService, IncomeService, DebtService, LoanOptionService } from 'app/routes/loan/shared/borrower.service';
import { HelperService } from 'app/routes/loan/shared/helper.service';

@Component({
    selector: 'app-income-debts-container',
    templateUrl: './income-debts-container.component.html',
    styleUrls: ['./income-debts-container.component.scss']
})
export class IncomeDebtsContainerComponent {
    @Input() viewMode: boolean;
    @Input()
    incomes: IIncome[];
    @Input()
    debts: IDebt[];
    @Input()
    borrowers: IBorrower[];
    @Input()
    totalIncome: number;
    @Input()
    totalDebts: number;
    @Input()
    loan: ILoan;
    incomeDebtsCollapsed = true;
    constructor(
        private borrowerService: BorrowerService,
        private helperService: HelperService,
        private incomeService: IncomeService,
        private debtService: DebtService,
        private loanOption: LoanOptionService
    ) {
        this.incomes = new Array<Income>();
        this.debts = new Array<Debt>();
        borrowerService.borrower$.subscribe(this.incomeStatusUpadteBasedOnBorrower.bind(this));
        incomeService.incomes$.subscribe(this.calculateTotalIncome.bind(this));
        debtService.debts$.subscribe(this.calculateTotalDebt.bind(this));

    }
    incomeStatusUpadteBasedOnBorrower(_borrower: IBorrower) {
        // if (!_borrower.isActive) {
        this.incomes.filter(x => x.borrowerId === _borrower.borrowerId).forEach(x => x.isActive = _borrower.isActive);
        this.debts.filter(x => x.borrowerId === _borrower.borrowerId).forEach(x => x.isActive = _borrower.isActive);
        // }
        this.calculateTotalIncome();
        this.calculateTotalDebt();
    }
    calculateTotalIncome() {
        const activeMembers = this.borrowers.filter(x => x.isActive);
        const activeIncomes = this.incomes.filter(x => x.isActive && activeMembers.filter(y => y.borrowerId === x.borrowerId).length > 0);
        let totalMonthlyIncome = 0;
        activeIncomes.forEach(income => {
            totalMonthlyIncome = totalMonthlyIncome + this.incomeToMonthly(income.amount, income.frequency);
        });
        this.loan.totalIncome = this.helperService.getMoney(totalMonthlyIncome);
        this.loanOption.loanOptionChange(`income:${this.totalIncome}`);
    }
    incomeToMonthly(amount, frequency) {
        amount = this.helperService.getMoney(amount);
        let monthlyAmount = 0;
        switch (frequency) {
            case 'Weekly':
                monthlyAmount = (amount * 52) / 12;
                break;
            case 'Every 2 Weeks':
                monthlyAmount = (amount * 26) / 12;
                break;
            case 'Twice a Month':
                monthlyAmount = amount * 2;
                break;
            case 'Monthly':
                monthlyAmount = amount;
                break;
            case 'Monthly Non-Taxable Income (grossed up @ 1.25%)':
                monthlyAmount = amount + (amount * (1.25 / 100));
                break;
            case 'Annual':
                monthlyAmount = amount / 12;
                break;

            default:
                break;
        }
        return this.helperService.getMoney(monthlyAmount);
    }
    calculateTotalDebt() {
        const activeMembers = this.borrowers.filter(x => x.isActive);
        const activeDebts = this.debts.filter(x => x.isActive && activeMembers.filter(y => y.borrowerId === x.borrowerId).length > 0);
        let totalMonthlyDebt = 0;
        activeDebts.forEach(debt => {
            totalMonthlyDebt = totalMonthlyDebt + this.incomeToMonthly(debt.amount, debt.frequency);
        });
        this.loan.totalDebts = this.helperService.getMoney(totalMonthlyDebt);
        console.log(this.totalDebts);
        this.loanOption.loanOptionChange(`debt:${this.totalDebts}`);
    }
}



