import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { Borrower, ShareLoanModel } from './../shared/models/index';
import { IncomeDetailsComponent } from './../income-details/income-details.component';
import { BorrowerService, LoanOptionService } from 'app/routes/loan/shared/borrower.service';
import { LoanService } from "app/routes/loan/shared/loan.service";
import { Router } from "@angular/router";


@Component({
    selector: 'app-borrower-details',
    templateUrl: './borrower-details.component.html',
    styleUrls: ['./borrower-details.component.scss']
})
export class BorrowerDetailsComponent implements OnInit {
    @Input() borrowers: Borrower[];
    @ViewChild(IncomeDetailsComponent) incomeDetails: IncomeDetailsComponent;
    constructor(
        private borrowerService: BorrowerService,
        private loanService: LoanService,
        private router: Router,
        private loanOption: LoanOptionService
    ) {
        this.borrowers = [];
    }
    ngOnInit() {
        if (this.borrowers.length === 0) {
            const borrower = new Borrower();
            borrower.type = 'B';
            borrower.borrowerId = 'B';
            borrower.isActive = true;
            this.borrowers.push(borrower);
        }
    }
    public addCoBorrower() {
        const borrower = new Borrower();
        const coBorrowers = this.borrowers.filter(x => x.type === 'C');
        borrower.borrowerId = `C${coBorrowers.length + 1}`;
        borrower.type = 'C';
        borrower.isActive = true;
        this.borrowers.push(borrower);
    }
    public shareLoan(email: string) {
        const model = new ShareLoanModel();
        model.email = email;
        const pieces = window.location.href.split('/');
        model.loanLink = `${window.location.origin}/#/loan-calculator/customer-view/${pieces[pieces.length - 1]}`;
        this.loanService.shareLoan(model).subscribe((x) => {
            console.log('loan shared');
        });

    }
    public creditScoreChange(creditscore) {
        this.loanOption.loanOptionChange(`credit score:${creditscore}`);
    }
    public borrowerStatusChange(_borrower: any) {
        this.borrowerService.borrowerChange(_borrower);
        // if (this.incomeDetails) {

        //     this.incomeDetails.borrowerStatusChange();
        // }
    }
}



