import { Component, Input, ViewChild, OnChanges } from '@angular/core';

import { LoanOption } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { ExcelFormulas } from 'app/routes/loan/shared/ExcelFormulasService';
@Component({
    selector: 'app-amortization-schedule',
    templateUrl: './amortization-schedule.component.html',
    styleUrls: ['./amortization-schedule.component.scss']
})
export class AmortizationScheduleComponent implements OnChanges {
    @Input() loanOptionDetails: LoanOption;
    public output: any;
    constructor(private helperService: HelperService
    ) {
    }
    ngOnChanges(changes: any) {
        if (this.loanOptionDetails !== undefined) {
            this.output = this.calculateSchedule(this.loanOptionDetails.firstMortgage.loanAmount,
                this.loanOptionDetails.firstMortgage.rate, this.loanOptionDetails.firstMortgage.term);
            console.log('loanOptionDetails ngOnChanges', this.loanOptionDetails, this.output);
        }
    }
    calculateSchedule(loanAmount, interestRate, numberOfPayments) {
        let output: any = {};
        const paymentsPerYear = 12;
        output.loanAmount = this.helperService.getMoney(loanAmount);
        output.interestRate = interestRate;
        output.paymentsPerYear = paymentsPerYear;
        const years = (numberOfPayments / paymentsPerYear);
        output.years = years;
        output.numberOfPayments = numberOfPayments;

        const payment = output.payment = ExcelFormulas.PMT(interestRate / 100 / paymentsPerYear, numberOfPayments, -loanAmount);

        const schedule = ExcelFormulas.AmortizationSchedule(loanAmount,
            interestRate,
            paymentsPerYear,
            years,
            payment);
        // output.schedule = [];
        schedule.forEach(n => {
            n.Principle = this.helperService.getMoney(n.Principle);
            n.Interest = this.helperService.getMoney(n.Interest);
            n.Remaining = this.helperService.getMoney(n.Remaining);
            const EndingBalance = n.Remaining - n.Principle;
            n.EndingBalance = this.helperService.getMoney(EndingBalance);
        });
        output.payment = this.helperService.getMoney(output.payment);
        output.schedule = schedule;
        // .map(function (n) {
        //     n.Principle = this.helperService.getMoney(n.Principle);
        //     n.Interest = this.helperService.getMoney(n.Interest);
        //     n.Remaining = this.helperService.getMoney(n.Remaining);
        //     return n;
        // });
        // output.schedule = schedule.forEach(function (n) {
        //     output.schedule.push({
        //         'Period': n[0],
        //         'Principle': n[1].toFixed(2),
        //         'Interest': n[2].toFixed(2),
        //         'Remaining': n[3].toFixed(2)
        //     });
        // });
        return output;
    }
}



