/* Based on 
 * - EGM Mathematical Finance class by Enrique Garcia M. <egarcia@egm.co>
 * - A Guide to the PMT, FV, IPMT and PPMT Functions by Kevin (aka MWVisa1)
 */
export const ExcelFormulas = {

    PVIF: function (rate, nper) {
        return Math.pow(1 + rate, nper);
    },

    FVIFA: function (rate, nper) {
        return rate == 0 ? nper : (this.PVIF(rate, nper) - 1) / rate;
    },

    PMT: function (rate, nper, pv, fv = 0, type = 0) {
        // if (!fv) fv = 0;
        // if (!type) type = 0;

        if (rate == 0) return -(pv + fv) / nper;

        var pvif = Math.pow(1 + rate, nper);
        var pmt = rate / (pvif - 1) * -(pv * pvif + fv);

        if (type == 1) {
            pmt /= (1 + rate);
        };

        return pmt;
    },

    IPMT: function (pv, pmt, rate, per) {
        var tmp = Math.pow(1 + rate, per);
        return 0 - (pv * tmp * rate + pmt * (tmp - 1));
    },

    PPMT: function (rate, per, nper, pv, fv, type) {
        if (per < 1 || (per >= nper + 1)) return null;
        var pmt = this.PMT(rate, nper, pv, fv, type);
        var ipmt = this.IPMT(pv, pmt, rate, per - 1);
        return pmt - ipmt;
    },

    DaysBetween: function (date1, date2) {
        var oneDay = 24 * 60 * 60 * 1000;
        return Math.round(Math.abs((date1.getTime() - date2.getTime()) / oneDay));
    },

    // Change Date and Flow to date and value fields you use
    XNPV: function (rate, values) {
        var xnpv = 0.0;
        var firstDate = new Date(values[0].Date);
        for (var key in values) {
            var tmp = values[key];
            var value = tmp.Flow;
            var date = new Date(tmp.Date);
            xnpv += value / Math.pow(1 + rate, this.DaysBetween(firstDate, date) / 365);
        };
        return xnpv;
    },

    XIRR: function (values, guess?) {
        if (!guess) { guess = 0.1; }

        let x1 = 0.0;
        let x2 = guess;
        let f1 = this.XNPV(x1, values);
        let f2 = this.XNPV(x2, values);

        for (let i = 0; i < 100; i++) {
            if ((f1 * f2) < 0.0) { break; }
            if (Math.abs(f1) < Math.abs(f2)) {
                f1 = this.XNPV(x1 += 1.6 * (x1 - x2), values);
            }
            else {
                f2 = this.XNPV(x2 += 1.6 * (x2 - x1), values);
            }
        };

        if ((f1 * f2) > 0.0) { return null; }

        const f = this.XNPV(x1, values);
        let rtb = 0, dx = 0;
        if (f < 0.0) {
            rtb = x1;
            dx = x2 - x1;
        }
        else {
            rtb = x2;
            dx = x1 - x2;
        };

        for (let i = 0; i < 100; i++) {
            dx *= 0.5;
            const x_mid = rtb + dx;
            const f_mid = this.XNPV(x_mid, values);
            if (f_mid <= 0.0) { rtb = x_mid; }
            if ((Math.abs(f_mid) < 1.0e-6) || (Math.abs(dx) < 1.0e-6)) { return x_mid; }
        };

        return null;
    },

    IRR: (cf: number[], numOfFlows: number) => {
        const LOW_RATE = 0.01;
        const HIGH_RATE = 0.5;
        const MAX_ITERATION = 1000;
        const PRECISION_REQ = 0.00000001;
        let i = 0, j = 0;
        const m = 0.0;
        let old = 0.00;
        let newVal = 0.00;
        let oldguessRate = LOW_RATE;
        let newguessRate = LOW_RATE;
        let guessRate = LOW_RATE;
        let lowGuessRate = LOW_RATE;
        let highGuessRate = HIGH_RATE;
        let npv = 0.0;
        let denom = 0.0;
        for (i = 0; i < MAX_ITERATION; i++) {
            npv = 0.00;
            for (j = 0; j < numOfFlows; j++) {
                denom = Math.pow((1 + guessRate), j);
                npv = npv + (cf[j] / denom);
            }
            /* Stop checking once the required precision is achieved */
            if ((npv > 0) && (npv < PRECISION_REQ)) {

                break;
            }
            if (old == 0)
            { old = npv; }
            else
            { old = newVal; }
            newVal = npv;
            if (i > 0) {
                if (old < newVal) {
                    if (old < 0 && newVal < 0)
                    { highGuessRate = newguessRate; }
                    else
                    { lowGuessRate = newguessRate; }
                }
                else {
                    if (old > 0 && newVal > 0)
                    { lowGuessRate = newguessRate; }
                    else
                    { highGuessRate = newguessRate; }
                }
            }
            oldguessRate = guessRate;
            guessRate = (lowGuessRate + highGuessRate) / 2;
            newguessRate = guessRate;
        }
        return guessRate;
    },
    RATE: function (periods, payment, present, future?, type?, guess?) {
        guess = (guess === undefined) ? 0.01 : guess;
        future = (future === undefined) ? 0 : future;
        type = (type === undefined) ? 0 : type;

        // Set maximum epsilon for end of iteration
        const epsMax = 1e-10;

        // Set maximum number of iterations
        const iterMax = 128;

        // Implement Newton's method
        let y, y0, y1, x0, x1 = 0,
            f = 0,
            i = 0;
        let rate = guess;
        if (Math.abs(rate) < epsMax) {
            y = present * (1 + periods * rate) + payment * (1 + rate * type) * periods + future;
        } else {
            f = Math.exp(periods * Math.log(1 + rate));
            y = present * f + payment * (1 / rate + type) * (f - 1) + future;
        }
        y0 = present + payment * periods + future;
        y1 = present * f + payment * (1 / rate + type) * (f - 1) + future;
        i = x0 = 0;
        x1 = rate;
        while ((Math.abs(y0 - y1) > epsMax) && (i < iterMax)) {
            rate = (y1 * x0 - y0 * x1) / (y1 - y0);
            x0 = x1;
            x1 = rate;
            if (Math.abs(rate) < epsMax) {
                y = present * (1 + periods * rate) + payment * (1 + rate * type) * periods + future;
            } else {
                f = Math.exp(periods * Math.log(1 + rate));
                y = present * f + payment * (1 / rate + type) * (f - 1) + future;
            }
            y0 = y1;
            y1 = y;
            ++i;
        }
        return rate;
    },
    AmortizationSchedule: function (loan_amount, interest_rate, payments_per_year, years, payment) {
        const schedule = [];
        let remaining = loan_amount;
        const number_of_payments = payments_per_year * years;

        for (let i = 1; i <= number_of_payments; i++) {
            const interest = remaining * (interest_rate / 100 / payments_per_year);
            const principle = (payment - interest);
            const row =
                {
                    'Period': i,
                    'Principle': principle > 0 ?
                        (principle < payment ? principle : payment) : 0,
                    'Interest': interest > 0 ? interest : 0,
                    'Remaining': remaining > 0 ? remaining : 0
                };


            // [i, principle > 0 ?
            //     (principle < payment ? principle : payment) : 0, interest > 0 ? interest : 0, remaining > 0 ? remaining : 0];
            schedule.push(row);
            remaining -= principle;
        }
        return schedule;
    }

};



