import { Component, Input, ViewChild, OnInit } from '@angular/core';

import { LoanOption, IPrepaids, PrePaids, ICredits, Credits } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
import { SettingsService } from "app/routes/loan/shared/settings.service";
@Component({
    selector: 'app-credits-settings',
    templateUrl: './credits-settings.component.html',
    styleUrls: ['./credits-settings.component.scss']
})
export class CreditsSettingsComponent implements OnInit {
    // @Input() loanOptionDetails: LoanOption;
    credits: ICredits;
    constructor(public bsModalRef: BsModalRef,
        private settingsService: SettingsService
    ) {
        this.credits = new Credits();
    }
    ngOnInit() {
        this.settingsService.getCredits().subscribe((credits) => {
            this.credits = Object.assign(this.credits, credits);
        });
    }
    saveCredits() {
        this.settingsService.saveCredits(this.credits).subscribe((resp) => {
            console.log('saveClosingCosts success', this.credits, resp);
            this.bsModalRef.hide();
        });
    }

}



