import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Loan, Income, Debt, LoanOption, ClosingCost, Borrower } from './shared/models/index';
import { LoanService } from './shared/loan.service';
import { Observable } from 'rxjs/Observable';
import { BasicDetailsComponent } from './basic-details/basic-details.component';
import { ActivatedRoute } from '@angular/router';
import { SettingsService } from "app/routes/loan/shared/settings.service";
import { LoanSaveService, MessageService } from "app/routes/loan/shared/borrower.service";
import { Subscription } from "rxjs/Rx";


@Component({
    selector: 'app-home',
    templateUrl: './loan.component.html',
    styleUrls: ['./loan.component.scss']
})
export class LoanComponent implements OnInit, OnDestroy {
    @ViewChild(BasicDetailsComponent) basicDetails: BasicDetailsComponent;
    loan: Loan = null;
    subscription: Subscription;
    loanChange(event) {
        this.loan = event;
    }
    viewMode: boolean = false;
    constructor(
        private loanService: LoanService,
        private route: ActivatedRoute,
        private settingsService: SettingsService,
        private loanSaveService: LoanSaveService,
        private messageService: MessageService
    ) {
        this.loan = new Loan();
        // this.loanSaveService.loans$.subscribe(x => this.loanSave.bind(this));
        this.subscription = this.messageService.getMessage().subscribe(message => { this.loanDetailsSave(); });
    }
    getloanDetails(loanId) {
        this.loanService.getById(loanId).subscribe((loan) => {
            this.loan = loan;
            this.loan.loanId = loanId;
            if (this.loan.totalDebts === undefined) {
                this.loan.totalDebts = 0;
            }
            if (this.loan.borrowers === undefined || this.loan.borrowers.length === 0) {
                this.loan.borrowers = [];
                const borrower = new Borrower();
                borrower.type = 'B';
                borrower.borrowerId = 'B';
                borrower.isActive = true;
                this.loan.borrowers.push(borrower);
            }
            if (this.loan.incomes === undefined || this.loan.incomes.length === 0) {
                this.loan.incomes = [];
                const income = new Income();
                income.isActive = true;
                this.loan.incomes.push(income);
            }
            if (this.loan.debts === undefined || this.loan.debts.length === 0) {
                this.loan.debts = [];
                const debt = new Debt();
                debt.isActive = true;
                this.loan.debts.push(debt);
            }
            if (this.loan.loanOptions === undefined || this.loan.loanOptions.length === 0) {
                this.loan.loanOptions = [];
                const loanOption = new LoanOption();
                loanOption.optionNo = 1;
                this.loadclosingcostsDefaults(loanOption);
                this.loadPrepaidsDefaults(loanOption);
                this.loadCreditsDefaults(loanOption);
                this.loan.loanOptions.push(loanOption);
            }
        });
    }
    loadPrepaidsDefaults(loanOption) {
        this.settingsService.getPrepaids().subscribe((prepaids) => {
            loanOption.prePaids = Object.assign(loanOption.prePaids, prepaids);
        });
    }
    loadCreditsDefaults(loanOption) {
        this.settingsService.getCredits().subscribe((credits) => {
            loanOption.credits = Object.assign(loanOption.credits, credits);
        });
    }
    loanDetailsSave() {
        console.log('loan sve');
        this.loanService.save(this.loan).subscribe((x) => {
            console.log('loan saved');
        });
    }
    ngOnInit() {

        // const k = this.route.params.subscribe(params => {
        //     debugger;
        //     const l = +params['loanId']; // (+) converts string 'id' to a number

        //     // In a real app: dispatch action to load the details here.
        // });
        this.route.params.subscribe(
            (params: { loanId: string }) => {
                if (params.loanId) {
                    this.getloanDetails(+params.loanId);
                    const path = this.route.snapshot.url[0].path;
                    if (path === 'customer-view') {
                        this.viewMode = true;
                    }
                    // this.loan = new Loan();
                    // this.loan.loanId = params.loanId;
                    // if (this.loan.incomes.length === 0) {
                    //     const income = new Income();
                    //     income.isActive = true;
                    //     this.loan.incomes.push(income);
                    // }
                    // if (this.loan.debts.length === 0) {
                    //     const debt = new Debt();
                    //     debt.isActive = true;
                    //     this.loan.debts.push(debt);
                    // }
                    // if (this.loan.loanOptions.length === 0) {
                    //     const loanOption = new LoanOption();
                    //     loanOption.optionNo = 1;
                    //     this.loan.loanOptions.push(loanOption);
                    // }
                    // this.basicDetails.basicDetailsChange();
                }
            }
        );

        // this.route.data.subscribe(
        //     (data: { loan: Loan }) => {
        //         if (data.loan) {
        //             this.loan = data.loan;
        //             this.basicDetails.basicDetailsChange();
        //         }
        //     }
        // );
        // this.loanService.getById(5).subscribe((loan) => {
        //     this.loan = Object.assign(this.loan, loan);
        //     if (this.loan.incomes.length === 0) {
        //         const income = new Income();
        //         income.isActive = true;
        //         this.loan.incomes.push(income);
        //     }
        //     if (this.loan.debts.length === 0) {
        //         const debt = new Debt();
        //         debt.isActive = true;
        //         this.loan.debts.push(debt);
        //     }
        //     if (this.loan.loanOptions.length === 0) {
        //         const loanOption = new LoanOption();
        //         loanOption.optionNo = 1;
        //         this.loan.loanOptions.push(loanOption);
        //     }
        //     this.basicDetails.basicDetailsChange();
        // });
    }
    loadclosingcostsDefaults(loanOption) {
        this.settingsService.getClosingCosts().subscribe((closingCosts) => {
            loanOption.closingCosts = Object.assign(loanOption.closingCosts, closingCosts);

            if (loanOption.closingCosts.length === 0) {
                const c1 = new ClosingCost();
                c1.name = 'Discount Points';
                c1.mode = 1;
                c1.rowNo = 1;
                loanOption.closingCosts.push(c1);
                const c2 = new ClosingCost();
                c2.name = 'Title Insurance';
                c2.mode = 2;
                c2.rowNo = 2;
                loanOption.closingCosts.push(c2);
                //FOR THE FHA and VA 
                const c3 = new ClosingCost();
                c3.name = 'UFMIP (FHA)';
                c3.mode = 1;
                c3.rowNo = 3;
                c3.value = 1.75;                
                loanOption.closingCosts.push(c3);

                const c4 = new ClosingCost();
                c4.name = 'VA - (Purchase)';
                c4.mode = 1;
                c4.rowNo = 4;
                c4.value = 2.15;
                loanOption.closingCosts.push(c4);
                const c5 = new ClosingCost();
                c5.name = 'VA - (Re-Finance)';
                c5.mode = 1;
                c5.rowNo = 5;
                c5.value = 0.500;
                loanOption.closingCosts.push(c5);
                //END FHA and VA 
                for (let index = 6; index <= 15; index++) {
                    const c = new ClosingCost();
                    c.rowNo = index;
                    loanOption.closingCosts.push(c);
                }
            }
        });
    }
    ngOnDestroy() {
        // unsubscribe to ensure no memory leaks
        this.subscription.unsubscribe();
    }

}



