export interface ILoanTypeList {
    id: number;
    name: string;
    type: string;
    firtstMortgagePercentage: number;
    secondMortgagePercentage: number;
}

const getMoney = (value: any) => {
    value = value || 0;
    return (Math.round(parseFloat(value) * 100)) / 100;
}
export class LoanTypeList implements ILoanTypeList {
    constructor(
        public id: number,
        public name: string,
        public type: string,
        public firtstMortgagePercentage: number,
        public secondMortgagePercentage: number

    ) {

    }
}
