export interface IMITypeList {
    id: number;
    name: string;
}
export class MITypeList implements IMITypeList {
    constructor(
        public id: number,
        public name: string

    ) {

    }
}
export const MI_TYPE_LIST: IMITypeList[] = [
    new MITypeList(1, 'MONTHLY'),
    new MITypeList(2, 'TAMI'),
    new MITypeList(3, 'NONE')
];

export interface ITitleInsurance {
    policiesUpToAndIncluding: number;
    basicPremium: number;
}
export class TitleInsurance implements ITitleInsurance {
    policiesUpToAndIncluding: number;
    basicPremium: number;
    constructor(param?: ITitleInsurance) {
        if (param) {
            this.basicPremium = getMoney(param.basicPremium);
            this.policiesUpToAndIncluding = getMoney(param.policiesUpToAndIncluding);
        }
    }
}
export interface ITitleInsuranceRange {
    policyRangeFrom: number;
    policyRangeTo: number;
    subtract: number;
    multiplyBy: number;
    add: number;
}


export class TitleInsuranceRange implements ITitleInsuranceRange {
    policyRangeFrom: number;
    policyRangeTo: number;
    subtract: number;
    multiplyBy: number;
    add: number;

    constructor(param?: ITitleInsuranceRange) {
        if (param) {
            this.policyRangeFrom = getMoney(param.policyRangeFrom);
            this.policyRangeTo = getMoney(param.policyRangeTo);
            this.subtract = getMoney(param.subtract);
            this.multiplyBy = getMoney(param.multiplyBy);
            this.add = getMoney(param.add);
        }
    }
}
export interface IBpmiAdjustment {
    adjustmentName: string;
    creditScoreFrom: number;
    creditScoreTo: number;
    bpmi: number;
}
export class BpmiAdjustment implements IBpmiAdjustment {
    adjustmentName: string;
    creditScoreFrom: number;
    creditScoreTo: number;
    bpmi: number;
    constructor(param?: IBpmiAdjustment) {
        if (param) {

            this.adjustmentName = param.adjustmentName;
            this.creditScoreFrom = getMoney(param.creditScoreFrom);
            this.creditScoreTo = getMoney(param.creditScoreTo);
            this.bpmi = getMoney(param.bpmi);
        }
    }
}
export interface IBpmi {
    ltvFrom: number;
    ltvTo: number;
    creditScoreFrom: number;
    creditScoreTo: number;
    bpmi: number;
    termFrom: number;
    termTo: number;
}
export class Bpmi implements IBpmi {
    termFrom: number;
    termTo: number;
    ltvFrom: number;
    ltvTo: number;
    creditScoreFrom: number;
    creditScoreTo: number;
    bpmi: number;
    constructor(param?: IBpmi) {
        if (param) {
            this.ltvFrom = getMoney(param.ltvFrom);
            this.ltvTo = getMoney(param.ltvTo);
            this.creditScoreFrom = getMoney(param.creditScoreFrom);
            this.creditScoreTo = getMoney(param.creditScoreTo);
            this.bpmi = getMoney(param.bpmi);
            this.termFrom = param.termFrom === null ? null : +param.termFrom;
            this.termTo = param.termTo === null ? null : +param.termTo;
        }
    }

}



