import { Component, Input, ViewChild, OnInit } from '@angular/core';

import { LoanOption, IPrepaids, PrePaids } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
import { SettingsService } from "app/routes/loan/shared/settings.service";
@Component({
    selector: 'app-prepaids-settings',
    templateUrl: './prepaids-settings.component.html',
    styleUrls: ['./prepaids-settings.component.scss']
})
export class PrepaidsSettingsComponent implements OnInit {
    // @Input() loanOptionDetails: LoanOption;
    prePaids: IPrepaids;
    constructor(public bsModalRef: BsModalRef,
        private settingsService: SettingsService
    ) {
        this.prePaids = new PrePaids();
    }
    ngOnInit() {
        this.settingsService.getPrepaids().subscribe((prePaids) => {
            this.prePaids = Object.assign(this.prePaids, prePaids);
        });
    }
    savePrepaids() {
        this.settingsService.savePrepaids(this.prePaids).subscribe((resp) => {
            console.log('saveClosingCosts success', this.prePaids, resp);
            this.bsModalRef.hide();
        });
    }

}



