import { Component, OnInit, Input } from '@angular/core';
import { IIncome, Income, IBorrower, IDebt, Debt } from './../shared/models/index';
import { BorrowerService, DebtService } from 'app/routes/loan/shared/borrower.service';
@Component({
    selector: 'app-debt-details',
    templateUrl: './debt-details.component.html',
    styleUrls: ['./debt-details.component.scss']
})
export class DebtDetailsComponent {

    @Input()
    debts: IDebt[];
    @Input()
    borrowers: IBorrower[];
    // frequency: string[] = ['Weekly', 'Every 2 Weeks'];

    constructor(
        private debtService: DebtService
    ) {
        this.debts = new Array<Debt>();
    }
    public addDebt() {
        const debt = new Debt();
        debt.isActive = true;
        this.debts.push(debt);
        this.debtService.debtChange(debt);
    }
    public debtChange(_debt: any) {
        this.debtService.debtChange(_debt);
    }
}



