import { Component, Input, ViewChild } from '@angular/core';

import { LoanOption } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
@Component({
    selector: 'app-amortization-schedule-container',
    templateUrl: './amortization-schedule-container.component.html',
    styleUrls: ['./amortization-schedule-container.component.scss']
})
export class AmortizationScheduleContainerComponent {
    borrowerCollapsed = true;
    @Input() loanOptionDetails: LoanOption;
    constructor(private helperService: HelperService,
        public bsModalRef: BsModalRef
    ) {
    }

}



