import { LoanOption, ILoanOption } from './index';
export interface IBorrower {
    id: string;
    borrowerId: string;
    name: string;
    email?: string;
    phone?: number;
    creditScore: number;
    /**
     * B:borrower;
     * C:CoBorrower
     */
    type: string;
    isActive: boolean;
}
export class Borrower implements IBorrower {
    id: string;
    borrowerId: string;
    name: string;
    email?: string;
    phone?: number;
    creditScore: number;
    /**
     * B:borrower;
     * C:CoBorrower
     */
    type: string;
    isActive: boolean;
    constructor() {
        this.email = '';
        this.name = '';
        this.phone = null;
        this.creditScore = 0;
    }
}
export interface IIncome {
    incomeId: string;
    incomeSource: string;
    frequency: string;
    borrowerId: string;
    amount?: number;
    isActive: boolean;
}
export class Income implements IIncome {
    incomeId: string;
    incomeSource: string;
    frequency: string;
    borrowerId: string;
    amount?: number;
    isActive: boolean;
    constructor() {
        this.incomeSource = '';
        this.frequency = '';
        this.borrowerId = 'B';
        this.amount = null;
    }
}

export interface IDebt {
    debtId: string;
    description: string;
    frequency: string;
    borrowerId: string;
    amount: number;
    isActive: boolean;
}
export class Debt implements IDebt {
    debtId: string;
    description: string;
    frequency: string;
    borrowerId: string;
    amount: number;
    isActive: boolean;
    constructor() {
        this.description = '';
        this.frequency = '';
        this.borrowerId = 'B';
        this.amount = null;
    }

}



export interface ILoan {
    loanId: string;
    qualifyingCreditScore: number;
    preApprovalCode: string;
    stateId: number;
    borrowers: IBorrower[];
    incomes: IIncome[];
    totalIncome: number;
    debts: IDebt[];
    totalDebts: number;
    loanOptions: ILoanOption[];

}
const getMoney = (value: any) => {
    value = value || 0;
    return (Math.round(parseFloat(value) * 100)) / 100;
}
export interface IShareLoan {
    email: string;
    loanLink: string;
}
export class ShareLoanModel implements IShareLoan {
    email: string;
    loanLink: string;
    constructor() {
        this.email = '';
        this.loanLink = '';
    }

}
export class Loan implements ILoan {
    totalIncome: number;
    totalDebts: number;
    loanOptions: ILoanOption[];
    debts: IDebt[];
    public loanId: string;
    public qualifyingCreditScore: number;
    public preApprovalCode: string;
    public stateId: number;
    public borrowers: IBorrower[];
    incomes: IIncome[];
    constructor() {
        // if (loan) {
        //     this.preApprovalCode = loan.preApprovalCode || '';
        //     this.borrowers = loan.borrowers || new Array<Borrower>();
        //     this.incomes = loan.incomes || new Array<Income>();
        //     this.debts = loan.debts || new Array<Debt>();
        //     this.loanOptions = loan.loanOptions || new Array<LoanOption>();
        //     this.totalIncome = getMoney(loan.totalIncome) || 0;
        //     this.totalDebts = getMoney(loan.totalDebts) || 0;
        //     this.qualifyingCreditScore = loan.qualifyingCreditScore || 0;
        //     this.stateId = loan.stateId || 0;
        // }
        // else {
        this.preApprovalCode = "";
        this.borrowers = new Array<Borrower>();
        this.incomes = new Array<Income>();
        this.debts = new Array<Debt>();
        this.loanOptions = new Array<LoanOption>();
        this.totalIncome = 0;
        this.totalDebts = 0;
        this.qualifyingCreditScore = 0;
        this.stateId = 0;
        // }
    }
}



