import { Component, OnInit, Input, OnChanges, DoCheck, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { Loan } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
@Component({
    selector: 'app-basic-details',
    templateUrl: './basic-details.component.html',
    styleUrls: ['./basic-details.component.scss']
})
export class BasicDetailsComponent implements OnInit, OnChanges {
    @Input() viewMode: boolean;
    borrowerCollapsed = true;
    @Input() basicDetails: Loan;
    basicDetailForm: FormGroup;
    createGroup(): any {
        // const group = this.fb.group({});
        // group.addControl(this.basicDetails.preApprovalCode, this.fb.control({}));
        // group.addControl(this.basicDetails.qualifyingCreditScore, this.fb.control({}));
        const group = this.fb.group({
            loanId: '',
            qualifyingCreditScore: '',
            preApprovalCode: '',
            stateId: '',
            borrowers: []
        });

        return group;
    }
    updateBasicDetails(values: Object) {
        (<any>Object).assign(this.basicDetails, values);
    }
    // loan = new Loan();
    constructor(
        private fb: FormBuilder,
        private helperService: HelperService
    ) {
        // console.log('loan', this.loan, this.basicDetails);
        this.basicDetailForm = this.createGroup();
        this.basicDetailForm.valueChanges.subscribe(value => this.updateBasicDetails(value));

    }
    getQualifyingCreditScore() {
        const activeBorrowers = this.basicDetails.borrowers.filter(x => x.isActive);
        const activeAmounts = activeBorrowers.map(x => x.creditScore);
        const sum = activeAmounts.reduce((a, b) => this.helperService.getSum(a, b), 0);
        const count = activeBorrowers.length;
        const avg = sum / count;
        this.basicDetails.qualifyingCreditScore = this.helperService.getMoney(avg);
        return this.basicDetails.qualifyingCreditScore;
    }
    basicDetailsChange() {
        this.basicDetailForm.patchValue(this.basicDetails);
    }
    ngOnInit() {
        // console.log('loan', this.loan, this.basicDetails);
        this.basicDetailForm.patchValue(this.basicDetails);
    }
    ngOnChanges(changes: any) {

        // console.log('loan', this.basicDetails);
        const { loanId, qualifyingCreditScore, preApprovalCode, stateId, borrowers } = this.basicDetails;

        // console.log('details',details);
        this.basicDetailForm.patchValue({ loanId, qualifyingCreditScore, preApprovalCode, stateId, borrowers });
    }

}



