import { NgModule } from '@angular/core';
import { LoanComponent } from './loan.component';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { BasicDetailsComponent } from './basic-details/basic-details.component';
import { IncomeDebtsContainerComponent } from './income-debts-container/income-debts-container.component';
import { IncomeDetailsComponent } from './income-details/income-details.component';

import { LoanOptionContainerComponent } from './loan-option-container/loan-option-container.component';
import { LoanOptionComponent } from './loan-option/loan-option.component';
import { BorrowerDetailsComponent } from './borrower-details/borrower-details.component';
import { SharedModule } from '../../shared/shared.module';
import { LoanService } from './shared/loan.service';
import { DebtDetailsComponent } from './debt-details/debt-details.component';
import { BorrowerService, IncomeService, DebtService, LoanOptionService, LoanSaveService } from 'app/routes/loan/shared/borrower.service';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BootstrapSwitchComponent } from 'angular2-bootstrap-switch';
import { CurrencyMaskModule } from 'ng2-currency-mask';

import { AmortizationScheduleComponent } from './amortization-schedule/amortization-schedule.component';
import { AmortizationScheduleContainerComponent } from './amortization-schedule-container/amortization-schedule-container.component';
import { PrepaidsSettingsComponent } from './prepaids-settings/prepaids-settings.component';
import { ClosingCostsSettingsComponent } from './closing-costs-settings/closing-costs-settings.component';
import { CreditsSettingsComponent } from './credits-settings/credits-settings.component';
import { SettingsService } from './shared/settings.service';

import { CurrencyMaskConfig, CURRENCY_MASK_CONFIG } from "ng2-currency-mask/src/currency-mask.config";

export const CustomCurrencyMaskConfig: CurrencyMaskConfig = {
    align: 'right',
    allowNegative: true,
    allowZero: true,
    decimal: '.',
    precision: 2,
    prefix: '',
    suffix: '',
    thousands: ','
};


const routes: Routes = [
    { path: ':loanId', component: LoanComponent },
    { path: 'customer-view/:loanId', component: LoanComponent },
];

@NgModule({
    imports: [
        SharedModule,
        RouterModule.forChild(routes),
        FormsModule,
        CommonModule,
        CurrencyMaskModule
    ],
    declarations: [BootstrapSwitchComponent, LoanComponent, BasicDetailsComponent, BorrowerDetailsComponent,
        IncomeDebtsContainerComponent, IncomeDetailsComponent, DebtDetailsComponent,
        LoanOptionContainerComponent, LoanOptionComponent, AmortizationScheduleContainerComponent, AmortizationScheduleComponent,
        PrepaidsSettingsComponent, ClosingCostsSettingsComponent, CreditsSettingsComponent
    ],
    providers: [
        { provide: CURRENCY_MASK_CONFIG, useValue: CustomCurrencyMaskConfig },
        LoanService, BorrowerService, HelperService, IncomeService, DebtService, LoanOptionService, SettingsService
    ],
    exports: [
        RouterModule, BasicDetailsComponent,
        BorrowerDetailsComponent,
        IncomeDebtsContainerComponent, IncomeDetailsComponent, DebtDetailsComponent,
        LoanOptionContainerComponent, LoanOptionComponent, AmortizationScheduleContainerComponent, AmortizationScheduleComponent,
        PrepaidsSettingsComponent, CreditsSettingsComponent
    ],
    entryComponents: [AmortizationScheduleContainerComponent,
        PrepaidsSettingsComponent, ClosingCostsSettingsComponent, CreditsSettingsComponent]
})
export class LoanModule { }



