import { Component, Input, ViewChild, OnInit } from '@angular/core';

import { LoanOption, IClosingCost, ClosingCost } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
import { SettingsService } from "app/routes/loan/shared/settings.service";
@Component({
    selector: 'app-closing-costs-settings',
    templateUrl: './closing-costs-settings.component.html',
    styleUrls: ['./closing-costs-settings.component.scss']
})
export class ClosingCostsSettingsComponent implements OnInit {
    // @Input() loanOptionDetails: LoanOption;

    closingCosts: IClosingCost[] = [];
    constructor(public bsModalRef: BsModalRef,
        private settingsService: SettingsService
    ) {
        const c1 = new ClosingCost();
        c1.name = 'Discount Points';
        c1.mode = 1;
        c1.rowNo = 1;
        this.closingCosts.push(c1);
        const c2 = new ClosingCost();
        c2.name = 'Title Insurance';
        c2.mode = 2;
        c2.rowNo = 2;
        this.closingCosts.push(c2);
        //FOR THE FHA and VA 
        const c3 = new ClosingCost();
        c3.name = 'UFMIP (FHA)';
        c3.mode = 1;
        c3.rowNo = 3;
        c3.percentage = 1.75;
        this.closingCosts.push(c3);

        const c4 = new ClosingCost();
        c4.name = 'VA - (Purchase)';
        c4.mode = 1;
        c4.rowNo = 4;
        c4.percentage = 2.15;
        this.closingCosts.push(c4);
        const c5 = new ClosingCost();
        c5.name = 'VA - (Re-Finance)';
        c5.mode = 1;
        c5.rowNo = 5;
        c5.percentage = 0.500;
        this.closingCosts.push(c5);
        //END FHA and VA 
        for (let index = 6; index <= 15; index++) {
            const c = new ClosingCost();
            c.rowNo = index;
            this.closingCosts.push(c);
        }
    }
    ngOnInit() {
        this.settingsService.getClosingCosts().subscribe((closingCosts) => {
            this.closingCosts = Object.assign(this.closingCosts, closingCosts);
            if (this.closingCosts.length === 0) {
                const c1 = new ClosingCost();
                c1.name = 'Discount Points';
                c1.mode = 1;
                c1.rowNo = 1;
                this.closingCosts.push(c1);
                const c2 = new ClosingCost();
                c2.name = 'Title Insurance';
                c2.mode = 2;
                c2.rowNo = 2;
                this.closingCosts.push(c2);
                //FOR THE FHA and VA 
                const c3 = new ClosingCost();
                c3.name = 'UFMIP (FHA)';
                c3.mode = 1;
                c3.rowNo = 3;
                c3.percentage = 1.75;
                this.closingCosts.push(c3);

                const c4 = new ClosingCost();
                c4.name = 'VA - (Purchase)';
                c4.mode = 1;
                c4.rowNo = 4;
                c4.percentage = 2.15;
                this.closingCosts.push(c4);
                const c5 = new ClosingCost();
                c5.name = 'VA - (Re-Finance)';
                c5.mode = 1;
                c5.rowNo = 5;
                c5.percentage = 0.500;
                this.closingCosts.push(c5);
                //END FHA and VA 

                for (let index = 6; index <= 15; index++) {
                    const c = new ClosingCost();
                    c.rowNo = index;
                    this.closingCosts.push(c);
                }
            }
        });
    }
    saveClosingCosts() {
        this.settingsService.saveClosingCosts(this.closingCosts).subscribe((resp) => {
            console.log('saveClosingCosts success', this.closingCosts, resp);
            this.bsModalRef.hide();
        });
    }

}



